export class User  {

  _id:number;
  _username: string;
  _email: string;

  setId(id:number) {
    this._id = id;
  }

  getId() {
    return this._id;
  }

  setUsername(username: string) {
    this._username = username;
  }

  getUsername(){
    return this._username;
  }

  setEmail(email: string) {
    this._email = email;
  }

  getEmail() {
    return this._email;
  }

}
